export default {
	data() {
		return {
			id: 0,
			useType:0,
			details:{}
		};
	},
	onLoad(options) {
		this.id = options.id
		this.useType=options.useType
	},
	onShow(){
		this.getDetai()
	},
	methods: {
		getDetai() {
			let self = this
			let params = {
				fetchImgList:true,
				fetchReceive :true,
				useType :this.useType,
				virtualId:this.id
			}
			this.$acFrame.HttpService.receiveDetail(params).then(res => {
				if (res.success) {
					self.details = res.data
					uni.setNavigationBarTitle({
						title:res.data.name
					})
				}
			})
		},
		setImg(src) {
			return this.$acFrame.Util.setImgUrl(src);
		},
		sureRward() {
		    	let self = this
		    	let params = {
		    		buyNum : 1,
		    		userCode : this.details.userCode,
		    		virtualId : this.details.id,
		    		sceneType: 3
		    	}
		    	this.$acFrame.HttpService.virtualBuy(params).then(res => {
		    		if (res.success) {
						uni.navigateTo({
							url:'/pages/myshop/payWay?order='+JSON.stringify(res.data)
						})
		    		}
		    	})
		},
	}
}
